.loading {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
    animation: loading 5s ease-in-out 0s infinite alternate-reverse;
}

.loading-text {
    text-align: center;
    padding: 0 .3rem;
    font-size: calc(1rem + 1vmin);
    color: #121212;
    font-weight: 500;
    font-family: 'Poppins';
    text-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    animation-name: loading;
    animation: loading-text 5s ease-in-out 0s infinite alternate;
    transition: all 500ms ease;
}

@keyframes loading-text {
    0% {
        color: #121212;
        font-family: 'Maconda';
    }

    25% {
        color: #202401;
        transform: scale(.9);
    }

    50% {
        color: #474d59;
        font-family: 'Roboto';
    }

    75% {
        color: rgb(83, 3, 94);
        transform: scale(.8);
    }

    100% {
        color: #641921;
        font-family: 'Poppins';
        transform: scale(1);
    }
}

@keyframes loading {
    0% {
        transform: scale(1);
        opacity: 1;
    }

    50% {
        transform: scale(.8) rotate(240deg);
        opacity: .75;
    }

    100% {
        opacity: .5;
        transform: scale(1) rotate(360deg);
    }
}

.active svg {
    background-color: #fff;
    box-shadow: 2px 1px 2px 1px #fda069;
    transition: all 500ms ease;
    border-radius: 2px;
}

.go-and-login {
    background-color: #fda069;
    text-align: center;
    margin: 1.5rem 1rem;
    padding: 20px;
    color: #121212;
    border-radius: 3%;
}

.go-and-login h2 {
    color: #641921;
    font-family: 'Impact';
    font-size: 1.8rem;
}